.back-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    font-size: 100px;
    background: rgb(14, 165, 233);
    height: 50px;
    width: 50px;
    color: white;
    cursor: pointer;
    border-radius: 100px;
    padding: 10px;
    border: none;
    box-shadow: 0 5px 10px #ccc;
}

.back-to-top:hover {
    background: #202F81;
}